.project-heading {
    text-align: center;
    padding: 4rem 0 2rem 0;
}

.project-container {
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 40px;
    padding-bottom: 5rem;
}

.project-card {
    background-color: #1a1919;
    padding: 1.2rem 1rem;
    width: 100%;
}

.project-card img {
    width: 100%;
    height: auto;
}

.project-title {
    color: white;
    padding: 1rem 0;
}

.project-title p {
    padding-bottom: 1rem;
    font-size: 1.1rem;
    text-align: justify;
}

.pro-details {
    line-height: 1.5rem;
    text-align: justify;
}

.pro-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem 0;
}

@media screen and (max-width: 915px) {
    .project-container {
        max-width: 90%;
        margin: auto;
        grid-template-columns: 1fr;
    }
}